<template>
  <div class="course_about_container">
    <div class="course_about_top">
      <div style="flex:1.3;margin-left:10px;font-size:18px;" v-on:click="backMine">&lt;</div>
      <div style="flex:2;font-size:18px;">我的关注</div>
    </div>
    <div class="course_about_content">
      <div style="width:100%;height:10px;background: background:rgb(240, 240, 240)"></div>
      <div class="course_about_content_items" v-for="(item,index) in list" :key="index">
        <div style="margin-left:20px;">
          <img :src="item.avatar" alt class="course_about_content_items_icon" />
        </div>
        <div class="course_about_content_items_right">
          <div>
            {{item.teacher_name}}
            <span
              style="margin-left:10px;color:rgb(252, 80, 5);"
            >{{item.level_name}}</span>
          </div>
          <div class="course_about_content_items_rigth_bottom">{{item.introduction}}</div>
        </div>
        <div>
          <span class="course_about_cancle"></span>
          <span class="cancle" v-on:click="cancleFocus(index)">取消关注</span>
        </div>
      </div>
      <div style="width:100%;text-align:center;font-size:14px;color:grey;">没有更多了</div>
    </div>

    <div class="course_about_none" v-show="showCourse"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [],
      showCourse: false,
      collectId: ""
    };
  },
  methods: {
    // collect_id
    cancleFocus(index) {
      this.$http
        .put(`/api/app/collect/cancel/${this.collectId}/2`)
        .then(res => {
          //  console.log(res)
          this.$http
            .get("/api/app/collect?page=1&limit=10&type=2&", {
              page: 1,
              limit: 10,
              type: 2
            })
            .then(res => {
              //   window.console.log(res.data.data)
              this.list = res.data.data.list;
            });
        });
    },
    backMine() {
      this.$router.back();
    }
  },
  mounted() {
    this.$http.get("/api/app/collect?page=1&limit=10&type=1&").then(res => {
        //   window.console.log(res.data.data)
        this.list = res.data.data.list;
        this.list.forEach(e => {
          this.collectId = e.collect_id;
        });
      });
  }
};
</script>

<style>
.course_about_top {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  background: white;
}
.course_about_content {
  width: 100%;
  height: 600px;
  background: rgb(240, 240, 240);
  margin: 0 auto;
}
.course_about_content_items {
  width: 94%;
  height: 70px;
  background: white;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-top: 10px;
  margin-left: 3%;
  border-radius: 5px;
}
.course_about_content_items_icon {
  width: 40px;
  height: 40px;
  margin-right: 10px;
}
.course_about_content_items_right {
  /* flex:4; */
  width: 60%;
  height: 70px;
  /* background: chocolate; */
}
.course_about_content_items_rigth_bottom {
  width: 100%;
  height: 35px;
  line-height: 35px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: 14px;
  color: grey;
}
.course_about_cancle {
  width: 100%;
}
.cancle {
  width: 70px;
  height: 30px;
  font-size: 14px;
  text-align: center;
  line-height: 30px;
  display: inline-block;
  color: rgb(252, 80, 5);
  background: rgb(253, 239, 229);
  border-radius: 20px;
}
</style>